<template>
  <div class="search-result">
    <div class="recommend" v-if="Recommend&&Recommend!=={}">
      <h4>你可能感兴趣</h4>
      <div class="artist" v-if="artist">
        <div class="img" :style="{backgroundImage:`url(${ExactSrc})`}"></div>
        <div class="artister">
          <div class="name">
            歌手：
            <span>{{ExactName}}</span>
          </div>
          <div class="other" v-if="artist">
            <span class="fans-size">粉丝：{{ExactFansSize}}</span>
            <span class="music-size">歌曲：{{MusicSize}}</span>
            <span class="album-size">专辑：{{AlbumSize}}</span>
          </div>
        </div>
      </div>
      <div class="album" v-if="album">
        <div class="img">
          <img :src="albumUrl" alt="">
        </div>
        <div class="infor">
          <p>专辑：{{albumName}}</p>
          <p>{{albumAthor}}</p>
        </div>
      </div>
    </div>
    <ul class="search-result-list">
      <ResultItem
        v-for="(item, index) in Result.songs"
        :Song="item"
        :key="index"
        :isShowIcon="true"
        @playSearch="$emit('playSearch',$event)"
      ></ResultItem>
    </ul>
  </div>
</template>

<script>
import ResultItem from "./ResultItem";
export default {
  name: "SearchResult",
  props: ["Result", "Recommend"],
  data() {
    return {
      artist: null,
      album: null
    };
  },
  watch: {
    Recommend() {
      this.artist = this.Recommend.artist;
      this.album = this.Recommend.album;
    }
  },
  computed: {
    albumAthor(){
      let n=''
      if(this.album){
        this.album.forEach(element => {
          n+=element.artists[0].name
        });
      }
      return n;

    },
    albumName(){
      let n=''
      if(this.album){
        this.album.forEach(element => {
          n+=element.name
        });
      }
      return n;

    },
    albumUrl(){
      let n=''
      if(this.album){
        this.album.forEach(element => {
          n+=element.picUrl
        });
      }
      return n;
    },
    AlbumSize() {
      let fans = "";
      if (this.artist) {
        this.artist.forEach(element => {
          fans += element.albumSize;
        });
      }
      return fans;
    },
    MusicSize() {
      let fans = "";
      if (this.artist) {
        this.artist.forEach(element => {
          fans += element.musicSize;
        });
      }
      return fans;
    },
    ExactName() {
      let fans = "";
      if (this.artist) {
        this.artist.forEach(element => {
          fans += element.name;
        });
      }
      return fans;
    },
    ExactSrc() {
      let fans = "";
      if (this.artist) {
        this.artist.forEach(element => {
          fans += element.picUrl;
        });
      }
      return fans;
    },
    ExactFansSize() {
      let fans = 0;
      if (this.artist) {
        this.artist.forEach(element => {
          fans += element.fansSize;
        });
      }

      fans =
        fans > 99999999
          ? (fans / 100000000).toFixed(1) + "亿"
          : fans > 9999
          ? (fans / 10000).toFixed(1) + "万"
          : fans;
      return fans;
    }
  },
  components: {
    ResultItem
  }
};
</script>

<style lang="less" scoped>
.search-result {
  .recommend {
    padding:10px 0; 
    h4{
      color:#6b6b6b;
    }
    .artist {
      display: flex;
      padding:10px 0;
      .img {
        width: 60px;
        height: 60px;
        border-radius: 50%;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
      }
      .artister {
        flex: 1;
        margin-left: 10px;
        div{
          line-height:30px;
          &.name{
            span{
              color:#25aff3;
            }
          }
          &.other{
            color:#6b6b6b;
            font-size:14px;
            span{
              margin-right: 10px;
              font-size:13px;
            }
          }
        }
      }
    }
    .album{
      display:flex;
      .img{
        height:60px;
        width:60px;
        img{
          width: 100%;
          display:block;
        }

      }
      .infor{
        flex:1;
        margin-left:10px;
        p{
          line-height: 30px;
          &:last-child{
            color:#25aff3;
            font-size:14px;

          }
        }
      }
    }
  }
}
</style>