<template>
  <div class="play-list">
    <playListTitle :detailTitle=" detailTitle"></PlayListTitle>
    <div class="list-content">
      <div class="title">
        <div class="icon"><i class="fa fa-play"></i>播放全部</div>
        <div class="collection"><span><i class="fa fa-plus"></i>收藏({{subscribedCount}})</span></div>
      </div>
      <ul class="list">
        <SongItem v-for="(item, index) in listDetails.tracks" :key="index" :Song="item" :index="index"
          @toPlay="$emit('toPlay',$event)" :isShowIcon="true"></SongItem>
      </ul>
    </div>
  </div>

</template>

<script>
  import playListTitle from '../components/PlayListTitle';
  import SongItem from '../components/SongItem';
  export default {
    name: "playlist",
    data() {
      return {
        listDetails: []
      };
    },
    computed: {
      detailTitle: function () {
        return {
          name: this.listDetails.name,
          bgUrl: this.listDetails.backgroundCoverUrl ? this.listDetails.backgroundCoverUrl : this.listDetails
            .coverImgUrl,
          updateFrequency: this.listDetails.updateFrequency,
          description: this.listDetails.description,
          commentCount: this.listDetails.commentCount,
          shareCount: this.listDetails.shareCount
        }
      },
      subscribedCount: function () {
        if (this.listDetails.subscribedCount > 99999999) {
          return (this.listDetails.subscribedCount / 100000000).toFixed(1) + "亿";
        }
        if (this.listDetails.subscribedCount > 9999) {
          return (this.listDetails.subscribedCount / 10000).toFixed(1) + "万";
        } else {
          return this.listDetails.subscribedCount
        }
      }
    },
    methods: {
      a: function (n) {
        window.console.log(this.$parent)
        this.$emit('xxx', n)
      },
      getPlayList: function (id) {
        this.axios
          .get("http://music.kele8.cn/playlist/detail", {
            params: {
              id: id
            }
          })
          .then(res => {

            this.listDetails = res.data.playlist;
          });
      }
    },

    created() {
      window.console.log("创建");
    },
    beforeRouteEnter(to, from, next) {
      next(vm => {
        vm.getPlayList(to.query.id);
      });
    },
    components: {
      playListTitle,
      SongItem
    }
  };
</script>

<style lang="less" scoped>
  .play-list {


    .play-list-title {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      z-index: -1;

    }

    .list-content {
      padding: 0 10px;
      background-color: #fff;
      border-top-left-radius: 20px;
      border-top-right-radius: 20px;
      margin-top: 220px;

      .title {
        font-size: 14px;
        display: flex;
        padding: 5px 0;
        line-height: 26px;

        div {
          flex: 1;
          color: #555;
          font-weight: bold;

          i {
            margin-right: 5px;
          }

          &.collection {
            text-align: right;
            font-weight: normal;

            span {
              padding: 5px 10px;
              background-color: #ff3a3a;
              color: #fff;
              font-size: 12px;
              border-radius: 20px;
            }
          }
        }
      }

      .list {
        height: 70vh;
        overflow: auto;
      }
    }

  }
</style>