<template>
  <div class="album-square">
    <div class="top">
      <span @click="$router.go(-1)">
        <i class="fa fa-arrow-left"></i>
      </span>
      <div class="title">更多新碟</div>
    </div>
    <div class="loading" v-if="isLoading">{{loadingText}}</div>
    <ul class="album-list" v-else>
      <AlbumItem v-for="(item, index) in albumArr" :key="index" :Album="item"></AlbumItem>
    </ul>

  </div>
</template>

<script>
  import AlbumItem from "../components/AlbumItem";

  export default {
    name: 'albumSquare',
    data() {
      return {
        albumArr: null,
        loadingText: '加载中',
        isLoading: false

      }
    },
    created() {
      this.isLoading = true
      this.axios.get(`https://music.kele8.cn/top/album`)
        .then(res => {
          this.albumArr = res.data.albums
          this.isLoading = false
        })

    },
    components: {
      AlbumItem
    }

  }
</script>

<style lang='less' scoped>
  .album-square {
    padding-top: 45px;

    .loading {
      text-align: center;
      padding: 20px 0;
      color: #6b6b6b;
    }

    .top {
      padding: 10px 15px;
      display: flex;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      z-index: 1;
      background-color: #fff;

      span {
        padding-right: 10px;
      }

      .title {
        flex: 1;
        font-weight: bold;
        font-size: 18px;
      }
    }

    .album-list {
      column-count: 3;
      column-gap: 10px;
      padding: 0 15px;

      li {
        width: 100%;
        break-inside: avoid;
      }
    }
  }
</style>