<template>
  <div class="search">
    <div class="top">
      <div class="go-back" @click="$router.go(-1)">
        <i class="fa fa-arrow-left"></i>
      </div>
      <div class="input">
        <input type="text" placeholder="单曲/歌手/专辑" v-model.trim="text" @keydown="keydown" />
        <div class="close" v-show="text" @click="clearInput">
          <i class="fa fa-times"></i>
        </div>
      </div>
      <div class="icon">
        <i class="fa fa-user"></i>
      </div>
    </div>
    <template v-if="!isShowResult">
      <Suggents :suggent="suggent" v-if="text" @searchSuggents="searchSuggent($event)" />
      <SearchHot
        v-else
        :suggent="suggent"
        :searchHots="searchHots"
        @searchHot="searchSuggent($event)"
      ></SearchHot>
    </template>
    <div class="result" v-else>
      <SearchResult :Result="SearchResult" :Recommend="Recommend" @playSearch="$emit('playSearch',$event)"></SearchResult>
    </div>
  </div>
</template>

<script>
import SearchHot from "../components/SearchHot";
import Suggents from "../components/Suggents";
import SearchResult from "../components/SearchResult";

export default {
  name: "Search",

  data() {
    return {
      text: "",
      SearchResult: {},
      suggent: [],
      searchHots: [],
      isShowResult: false,
      Recommend:null
    };
  },
  created() {
    this.axios.get("https://music.kele8.cn/search/hot").then(res => {
      this.searchHots = res.data.result.hots;
    });
  },
  watch: {
    text(newValue) {
      if (newValue) {
        this.axios
          .get(`http://music.kele8.cn/search/suggest`, {
            params: {
              keywords: this.text,
              type: "mobile"
            }
          })
          .then(res => {
            ({ allMatch: this.suggent } = res.data.result);
          });
      } else {
        this.isShowResult = false;
      }
    }
  },
  methods: {
    searchSuggent($event) {
      this.axios
        .get(`http://music.kele8.cn/search?keywords=${$event}`, {
          params: {
            limit: 20
          }
        })
        .then(res => {
          this.SearchResult = res.data.result;
        });
      this.text = $event;
      this.isShowResult = true;
    },
    keydown(e) {
      if (e && e.keyCode == 13) {
        this.axios
          .get(`http://music.kele8.cn/search?keywords=${this.text}`, {
            params: {
              limit: 20
            }
          })
          .then(res => {
            this.SearchResult = res.data.result;
          });
          this.axios.get(`http://music.kele8.cn/search/multimatch`,{
            params:{
              keywords: this.text
            }
          })
          .then(res => {
            this.Recommend=res.data.result
          })
          
        this.isShowResult = true;
      }
    },
    clearInput: function() {
      this.text = "";
    }
  },
  components: {
    SearchHot,
    Suggents,
    SearchResult
  }
};
</script>

<style lang="less" scoped>
.search {
  padding: 0 15px;
  .top {
    display: flex;
    color: #6b6b6b;
    padding: 10px 0;
    .go-back {
      text-align: left;
      padding-right: 15px;
      font-size: 20px;
      line-height: 23px;
    }
    .icon {
      text-align: right;
      padding-left: 15px;
      font-size: 20px;
      line-height: 23px;
    }
    .input {
      flex: 1;
      position: relative;
      .close {
        position: absolute;
        top: 0;
        right: 5px;
        font-weight: normal;
      }
      input {
        width: 100%;
        outline: none;
        padding: 5px 20px 5px 0;
        color: #6b6b6b;
        border: none;
        border-bottom: 2px solid #b4b4b4;
      }
    }
  }
}
</style>