<template>
  <div class="square">
    <div class="top">
      <span @click="$router.go(-1)">
        <i class="fa fa-arrow-left"></i>
      </span>
      <div class="title">歌单广场</div>
    </div>
    <div class="loading" v-if="isLoading">{{loadingText}}</div>
    <ul class="list" v-else>
      <SongListItem v-for="(item, index) in SquareData" :key="index" :item="item" @showSetails="showSetails($event)">
      </SongListItem>
    </ul>
  </div>
</template>

<script>
  import SongListItem from "../components/SongListItem";
  export default {
    name: "SongListSquare",
    data() {
      return {
        SquareData: null,
        loadingText: '加载中',
        isLoading: false
      }
    },
    methods: {
      showSetails: function (id) {
        this.$router.push({
          path: "playlist",
          query: {
            id: id
          }
        });
      }
    },
    created() {
      this.isLoading = true
      this.axios.get('https://music.kele8.cn/personalized')
        .then(res => {
          this.SquareData = res.data.result
          this.isLoading = false
        })

    },
    components: {
      SongListItem
    }
  };
</script>

<style lang="less" scoped>
  .square {
    padding-top: 45px;

    .loading {
      text-align: center;
      padding: 20px 0;
      color: #6b6b6b;
    }

    .top {
      padding: 10px 15px;
      display: flex;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      z-index: 1;
      background-color: #fff;

      span {
        padding-right: 10px;
      }

      .title {
        flex: 1;
        font-weight: bold;
        font-size: 18px;
      }
    }

    .list {
      display: flex;
      flex-wrap: wrap;
      padding: 0 15px;

      li {
        flex: 1 0 30%;

        &:nth-child(3n + 2) {
          margin: 0 5px;
        }
      }
    }
  }
</style>