<template>
  <li class="result-item" @click="$emit('playSearch',Song)">
    <div class="information">
      <p class="name">{{Song.name}}</p>
      <p class="athor">{{artist}}</p>
    </div>
    <div class="icon" v-if="isShowIcon">
      <i class="fa fa-play-circle" @click="$emit('playSearch',Song)"></i>
      <i class="fa fa-ellipsis-v"></i>
    </div>
  </li>
</template>

<script>
export default {
  name: "ResultItem",
  props: ["Song", "isShowIcon"],
  computed: {
    artist() {
      let a = "";
      this.Song.artists.forEach(elem => {
        a += elem.name + " ";
      });
      return a;
    }
  }
};
</script>

<style lang="less" scoped>
.result-item {
  display: flex;
  padding: 10px 0;
  color: #333;
  .information {
    flex: 1;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
    .name{
    overflow: hidden;

    }
    .athor {
      font-size: 14px;
      color: #999;
    overflow: hidden;

    }
  }
  .icon {
    color: #999;
    i {
      margin: 0 10px;
    }
  }
}
</style>