<template>
  <div class="recentList">
    <div class="top" v-if="!ShowFull">
      <span @click="$router.push('/mine')">
        <i class="fa fa-arrow-left"></i>
      </span>
      <div class="title">最近播放</div>
    </div>
    <ul class="list">
      <SongItem v-for="(item, index) in recentList" :key="index" :Song="item" :index="index" :isShowIcon="true"
        @toPlay="$emit('toPlay',$event)" @deleteSong="$emit('deleteSong',$event)"></SongItem>
    </ul>
  </div>
</template>

<script>
  import SongItem from "@/components/SongItem";
  export default {
    name: 'recentList',
    data() {
      return {
        recentList: [],
      }
    },
    computed: {
      ShowFull() {
        return this.$parent.ShowFull
      }
    },
    created() {
      this.recentList = this.$parent.PlayingArr.slice(0, 20)
    },
    components: {
      SongItem
    }
  }
</script>

<style lang="less" scoped>
  .recentList {
    padding-top: 60px;
    .top {
      padding: 10px 15px;
      display: flex;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      z-index: 1;
      background-color: #fff;

      span {
        padding-right: 10px;
      }

      .title {
        flex: 1;
        font-weight: bold;
      }
    }

    .list {
      padding: 0 15px;
    }
  }
</style>